
const domain = 'https://www.forum.mista.ru';

const api = {

    /*
    username -  ()  (!);
    password -   (!);
    */
    login: `${domain}/ajax_login.php`, 

    /*
    topics -  ,    (  - 20);
    utime -  (  unixtime)     ;
    callback -  ,      ,      ( JSONP);
    forum -   (1c|it|life);
    section_short_name=politic -     
    mytopics=1 -    
    user_id -   
    */
    topicsList: `${domain}/api/topic.php`,

    /*
    id -  (topic_id)  (!)
    */
    topicInfo: `${domain}/ajax_gettopic.php`,

    /*
    id -  (topic_id)  (!)
    from -   .   ,   
    to -   .   ,  from+10
    */    
    topicMessages: `${domain}/api/message.php`,

    /*
    POST
    {
        topic_id -  (topic_id)  (!)   
    }
    */
    addBookmark: `${domain}/ajax_addbookmark.php`,

    sections: `${domain}/ajax_getsectionslist.php`,

    /*
    POST
    {
        topic_id -  (topic_id)  (!)   
    }
    */
    newTopic: `index.php`,

     /*
    POST
    message_text,
    action=new,
    topic_id,
    user_name,
    rnd
    */
    postNewMessage: `${domain}/ajax_newmessage.php`,

}

export default api;


